import {
  all,
  fork,
  takeLatest,
  takeEvery,
  call,
  put,
  delay
} from "redux-saga/effects";
import {
  LOG_IN_REQUEST,
  LOG_IN_SUCCESS,
  LOG_IN_FAILURE,
  SIGN_UP_REQUEST,
  SIGN_UP_SUCCESS,
  SIGN_UP_FAILURE
} from "../reducers/user";
import axios from "axios";

// ì„œë²„ì— ìš”ì²­ì„ ë³´ëƒ„
function loginAPI() {
  return axios.post("/login");
}

function* login() {
  try {
    // yield call(loginAPI);
    yield delay(2000);
    yield put({
      // putì€ dispatchì™€ ë™ì¼
      type: LOG_IN_SUCCESS
    });
  } catch (e) {
    // loginAPI ì‹¤íŒ¨
    console.error(e);
    yield put({
      type: LOG_IN_FAILURE
    });
  }
}

function* watchLogin() {
  //  talkeLatestëŠ” LOG_IN ì•¡ì…˜ì´ ë””ìŠ¤íŒ¨ì¹˜ ë  ë•Œê¹Œì§€ ê¸°ë‹¤ë ¸ë‹¤
  //  ë””ìŠ¤íŒ¨ì¹˜ ë¬ì„ ë•Œ login ì œë„ˆë ˆì´í„° í˜¸ì¶œ
  yield takeLatest(LOG_IN_REQUEST, login);
}

function signUpAPI() {
  return axios.post("/login");
}

function* signUp() {
  try {
    yield call(signUpAPI);
    yield delay(2000);
    // throw new Error("Sign Up Error :(");
    yield put({
      // putì€ dispatch ë™ì¼
      type: SIGN_UP_SUCCESS
    });
  } catch (e) {
    // loginAPI ì‹¤íŒ¨
    console.error(e);
    yield put({
      type: SIGN_UP_FAILURE,
      error: e
    });
  }
}

function* watchSignUp() {
  yield takeEvery(SIGN_UP_REQUEST, signUp);
}

export default function* userSaga() {
  yield all([fork(watchLogin), fork(watchSignUp)]);
}
